import { Chart, registerables } from 'chart.js';
import zoomPlugin from 'chartjs-plugin-zoom';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import ChartJsWrapper from './components/ChartJsWrapper.vue';
import ScatterPlotWrapper from './components/ScatterPlotWrapper.vue';
import DistributionChart from './components/DistributionChart.vue';
import LineChart from './components/LineChart.vue';
import BarChart from './components/BarChart.vue';
import MultiSeriesChart from './components/MultiSeriesChart.vue';
import UniformXDependentChart from './components/UniformXDependentChart.vue';

import * as Colors from './assets/colors';

export { Colors };

export * from './enums';
export * from './implementations';
export * from './interfaces';
export {
  ChartJsWrapper,
  ScatterPlotWrapper,
  DistributionChart,
  LineChart,
  BarChart,
  MultiSeriesChart,
  UniformXDependentChart,
};

Chart.register(...registerables, zoomPlugin, ChartDataLabels);
