import { PointStyle } from 'chart.js';
import { IPointStyleProvider } from '../../interfaces/providers/IPointStyleProvider';
import PointStyleType from '../../enums/PointStyleType';

export class CyclePointStyleProvider implements IPointStyleProvider {
  constructor(styles?: PointStyle[]) {
    this.styles = styles ?? Object.values(PointStyleType);
    if (this.styles.length === 0) {
      this.styles.push('circle');
    }
  }

  private current = -1;

  private styles: PointStyle[];

  public getNextStyle(): PointStyle {
    this.current += 1;
    if (this.current >= this.styles.length) {
      this.current = 0;
    }

    return this.styles[this.current];
  }
}
