import { ChartOptions } from 'chart.js';
import { CreateDefaultChartPointOptions } from './DefaultChartPointOptions';
import { CreateDefaultTitleOptions } from '../title/DefaultChartTitleOptions';
import { CreateDefaultTooltipOptions } from '../tooltip/DefaultChartTooltipOptions';

export function CreateDefaultChartOptions(): ChartOptions {
  const chartOptions: ChartOptions = {
    maintainAspectRatio: false,
    responsive: true,
    events: ['mousemove', 'mouseout', 'click', 'touchstart', 'touchmove'],

    animation: {
      duration: 0.0,
    },
    elements: {
      point: CreateDefaultChartPointOptions(),
    },
    plugins: {
      datalabels: {
        display: false,
      },
      title: CreateDefaultTitleOptions(),
      tooltip: CreateDefaultTooltipOptions(),
      zoom: {
        zoom: {
          wheel: {
            enabled: true,
          },
          pinch: {
            enabled: true,
          },
          mode: 'xy',
        },
      },
    },
  };

  return chartOptions;
}
