<template>
  <chart-js-wrapper
    :data="chartData"
    :type="type"
    :options="chartOptions"
    :plugins="plugins"
    :dragYaxis="dragYaxis"
    :draggableDataSet="draggableDataSet"
    @valueChanged="valueChanged"
  />
</template>

<script lang="ts">
/* This LineChart.vue component is used to display distributions of the parameters */
import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';
import {
  Chart,
  ChartData,
  ChartDataset,
  ChartOptions,
  CoreScaleOptions,
  ChartType,
  Plugin,
  Scale,
  ScaleType,
  ScatterDataPoint,
} from 'chart.js';
import ChartJsWrapper from './ChartJsWrapper.vue';
import { IChartSeries } from '../interfaces/data/IChartSeries';

@Component({
  components: {
    ChartJsWrapper,
  },
})
export default class LineChart extends Vue {
  @Prop({ required: true })
  chartSeries!: IChartSeries[];

  @Prop({ default: '' })
  yAxisLabel!: string;

  @Prop({ default: 'linear' })
  yAxisScale!: ScaleType;

  @Prop({ default: '' })
  xAxisLabel!: string;

  @Prop({ default: 'linear' })
  xAxisScale!: ScaleType;

  @Prop({ default: true })
  forceXAxisMinZero!: boolean;

  @Prop({ default: 0 })
  dataPointSize!: number;

  @Prop({ default: 'line' })
  type!: ChartType;

  @Prop({ default: () => [] })
  plugins!: Plugin[];

  @Prop({ default: false })
  dragYaxis!: boolean;

  @Prop({ default: undefined }) yScaleMax!: number;

  @Prop({ default: undefined }) yScaleMin!: number;

  @Prop({ default: 0 }) draggableDataSet!: number;

  chartFontSize = 14;

  chart!: Chart;

  get chartData(): ChartData {
    if (this.chartSeries.length === 0) {
      return {
        labels: [],
        datasets: [],
      };
    }

    return {
      labels: this.chartSeries[0].axisLabels || [],
      datasets: this.chartSeries.map(
        (el): ChartDataset<'line'> => {
          // filter out 0s for logarithmic data points. Only do this if necessary to save in efficiency.
          // NOTE(quinton): if the values of the Data are not 'x' and 'y', this will break
          let logFilter = el.data;
          const isScatter = (el.data[0] as ScatterDataPoint).x !== undefined;

          if (this.xAxisScale === 'logarithmic' && isScatter) {
            logFilter = logFilter.filter((elMember) => (elMember as ScatterDataPoint).x >= 0);
          }
          if (this.yAxisScale === 'logarithmic' && isScatter) {
            logFilter = logFilter.filter((elMember) => (elMember as ScatterDataPoint).y >= 0);
          }

          if (logFilter.length < el.data.length) {
            // eslint-disable-next-line no-console
            console.warn('Logarithmic scale was used on LineChart axes; data points with value 0 have been removed');
          }

          return {
            data: logFilter,
            label: el.label,
            backgroundColor: el.backgroundColor,
            borderColor: el.borderColor,
            fill: el.fill,
            tension: el.useStraightLines ? 0 : undefined,
            stepped: el.stepped,
            pointRadius: el.pointRadius,
          };
        },
      ),
    };
  }

  get chartOptions(): ChartOptions {
    return {
      responsive: true,
      maintainAspectRatio: false,
      layout: {
        padding: {
          left: 0,
        },
      },
      scales: {
        x: this.xAxisProperties,
        y: this.yAxisProperties,
      },
      elements: {
        point: {
          radius: this.dataPointSize, // Removes the data p oints
        },
      },
      plugins: {
        datalabels: {
          display: false,
        },
        legend: {
          display: true,
          labels: {
            font: {
              size: this.chartFontSize,
            },
            filter: (item) => {
              return item.text.length > 0;
            },
          },
        },
        tooltip: {
          enabled: false,
        },
      },
      animation: {
        duration: 0,
      },
    };
  }

  get xAxisProperties() {
    return {
      type: this.xAxisScale,
      suggestedMin: this.forceXAxisMinZero ? 0 : undefined,
      ticks: {
        autoSkip: false,
        font: {
          size: this.chartFontSize,
        },
        callback: (value: string | number) => {
          if (Number.isNaN(Number(value)) || (this as any).type === 'category') {
            return value;
          }

          if (
            (this as any).type === 'logarithmic' &&
            Math.round(Math.log10(Number(value))) !== Math.log10(Number(value))
          ) {
            return ''; // This is to eliminate any log label that isn't a power of ten
          }
          if ((value < 0.001 && value > 0) || value >= 1e4) {
            return (value as number).toExponential(1);
          }
          return Number((value as number).toPrecision(3)).toString();
        },
      },
      title: {
        text: LineChart.formatLabel(this.xAxisLabel),
        font: {
          size: this.chartFontSize + 2,
          weight: 'bold',
        },
        display: this.xAxisLabel.length > 0,
      },
      /* eslint no-param-reassign: ["error", { "props": false }] */
      afterDataLimits(axis: Scale<CoreScaleOptions>) {
        if (axis.type === 'time') {
          axis.min -= 60 * 60 * 1000; // 1 hr (in milliseconds) before
        } else if (axis.min > 0) {
          axis.min *= 0.999;
        }

        // If the max is 1, that's likely because it's a fraction distribution and we don't need the extra space
        if (axis.type === 'time') {
          axis.max += 60 * 60 * 1000; // 1 hr (in milliseconds) later
        } else if (Math.abs(axis.max - 1) < 1e-8) {
          axis.max = 1;
        }
      },
    };
  }

  get yAxisProperties() {
    return {
      type: this.yAxisScale,
      max: this.yScaleMax,
      min: this.yScaleMin,
      ticks: {
        font: {
          size: this.chartFontSize,
        },
        callback(value: string | number) {
          if (Number.isNaN(Number(value)) || (this as any).options.type === 'category') {
            return value;
          }

          if (
            (this as any).type === 'logarithmic' &&
            Math.round(Math.log10(Number(value))) !== Math.log10(Number(value))
          ) {
            return ''; // This is to eliminate any log label that isn't a power of ten
          }
          if ((value < 0.001 && value > 0) || value >= 1e4) {
            return (value as number).toExponential(1);
          }
          return Number((value as number).toPrecision(3)).toString();
        },
      },
      grid: {
        tickLength: 5,
      },
      title: {
        text: LineChart.formatLabel(this.yAxisLabel),
        font: {
          size: this.chartFontSize + 2,
          weight: 'bold',
        },
        display: this.yAxisLabel.length > 0,
      },
      afterDataLimits(axis: Scale<CoreScaleOptions>) {
        if (axis.min > 0) {
          axis.min *= 0.999;
        }
      },
    };
  }

  resetChart() {
    if (this.$refs.chart) {
      (this.$refs.chart as any).resetChart();
    }
  }

  // eslint-disable-next-line class-methods-use-this
  static formatLabel(label: string) {
    if (label.includes('^2')) {
      const split = label.split('^2');
      // eslint-disable-next-line prefer-template
      return split[0] + '\u00B2' + split[1].substring(0);
    }
    if (label.includes('^3')) {
      const split = label.split('^3');
      // eslint-disable-next-line prefer-template
      return split[0] + '\u00B3' + split[1].substring(0);
    }
    return label;
  }

  valueChanged(index: number, datasetIndex: number, chart: Chart): void {
    this.$emit('valueChanged', index, datasetIndex, chart);
  }
}
</script>
