<template>
  <line-chart
    ref="distChart"
    :chart-series="dataSeries"
    :x-axis-label="xAxisLabel"
    :y-axis-label="yAxisLabel"
    :x-axis-scale="xAxisScale"
    :y-axis-scale="yAxisScale"
    :force-x-axis-min-zero="forceXAxisMinZero"
    :data-point-size="dataPointSize"
  />
</template>

<script lang="ts">
// eslint-disable-next-line import/no-unresolved, import/extensions
import { Vue, Component, Prop } from 'vue-property-decorator';
import { ChartType } from 'chart.js';
import { IChartSeries } from '../interfaces/data/IChartSeries';
import { IChartPoint } from '../interfaces/data/IChartPoint';
import LineChart from './LineChart.vue';
import * as Colors from '../assets/colors';

@Component({ components: { LineChart } })
export default class UniformXDependentChart extends Vue {
  @Prop({ required: true })
  data!: IChartPoint[][][];

  @Prop({ default: 'line' }) requestedChartType!: ChartType;

  @Prop({ default: '' }) xAxisLabel!: string;

  @Prop({ default: '' }) yAxisLabel!: string;

  @Prop({ default: 'linear' }) xAxisScale!: string;

  @Prop({ default: 'linear' }) yAxisScale!: string;

  @Prop({ default: true }) forceXAxisMinZero!: boolean;

  @Prop({ default: false }) showDataPoints!: boolean;

  sliderValue = 1;

  get dataPointSize(): number {
    return this.showDataPoints ? 3 : 0;
  }

  get dataSeries(): IChartSeries[] {
    const series: IChartSeries[] = [];
    this.data.forEach((p, s) => {
      p.map((data) => {
        return {
          data,
          ...UniformXDependentChart.multiSeriesDefaultChartProperties(s),
        } as IChartSeries;
      }).forEach((d) => {
        series.push(d);
      });
    });
    return series;
  }

  /* In the case of a distribution sample method returning multiple data sets (e.g. TwoUniform),
      this method is used so that the series backgrounds match, but the subseries have different line colors */
  static multiSeriesDefaultChartProperties(seriesIndex: number): Record<string, unknown> {
    return {
      label: '',
      backgroundColor: Colors.COLOR_ARRAY_T[seriesIndex],
      borderColor: Colors.COLOR_ARRAY_T[seriesIndex],
      fill: 'shape',
      pointRadius: 0,
      useStraightLines: true,
      stepped: false,
    };
  }
}
</script>

<style scoped>
#chart {
  flex: 1;
  min-height: 300px;
  max-height: 500px;
}

#error {
  color: red;
  font-size: 1rem;
  font-weight: bold;
  text-align: left;
}
</style>
