<template>
  <chart-js-wrapper :data="data" :type="type" :options="options" :plugins="plugins" />
</template>

<script lang="ts">
import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';
import { ChartData, ChartOptions, ChartType, Plugin } from 'chart.js';
import ChartJsWrapper from './ChartJsWrapper.vue';
import { CreateDefaultChartOptions } from '../implementations';

@Component({ components: { ChartJsWrapper } })
export default class ScatterPlotWrapper extends Vue {
  @Prop({ required: true })
  data!: ChartData;

  @Prop({ default: 'line' })
  type!: ChartType;

  @Prop({
    default: () => {
      return CreateDefaultChartOptions();
    },
  })
  options!: ChartOptions;

  @Prop({
    default: () => {
      return [];
    },
  })
  plugins!: Plugin[];
}
</script>
<style scoped lang="scss">
.disabled-tool-tip {
  cursor: no-drop;
}
</style>
