import { ChartDataset, PointStyle, ScatterDataPoint } from 'chart.js';
import { IColorProvider } from '../../interfaces/providers/IColorProvider';
import { IPointStyleProvider } from '../../interfaces/providers/IPointStyleProvider';
import { ConstantPointStyleProvider } from '../providers';

export const ScatterChartDatasetDefaultSettings: Partial<ChartDataset<'scatter'>> = {
  cubicInterpolationMode: 'default',
  pointRadius: 6.0,
  tension: 0.0,
  pointStyle: 'circle',
  hidden: false,
  showLine: true,
};

export function CreateScatterChartDataset(
  data: ScatterDataPoint[],
  label: string,
  colorProvider: IColorProvider,
  pointStyleProvider?: IPointStyleProvider,
  backgroundColor?: string,
  borderColor?: string,
): ChartDataset {
  const pointStyle: IPointStyleProvider =
    pointStyleProvider ?? new ConstantPointStyleProvider(ScatterChartDatasetDefaultSettings.pointStyle as PointStyle);

  const lineColor: string = colorProvider.getNextColor();

  const dataset: ChartDataset = {
    data,
    label,

    backgroundColor: backgroundColor ?? lineColor,
    borderColor: borderColor ?? lineColor,

    pointStyle: pointStyle.getNextStyle(),

    cubicInterpolationMode: ScatterChartDatasetDefaultSettings.cubicInterpolationMode,
    pointRadius: ScatterChartDatasetDefaultSettings.pointRadius,
    tension: ScatterChartDatasetDefaultSettings.tension,
    hidden: ScatterChartDatasetDefaultSettings.hidden,
    showLine: ScatterChartDatasetDefaultSettings.showLine,
  };

  return dataset;
}
