// needed for iteration by CyclePointStyleProvider
enum PointStyleType {
  Circle = 'circle',
  Cross = 'cross',
  CrossRot = 'crossRot',
  Dash = 'dash',
  Line = 'line',
  Rect = 'rect',
  RectRounded = 'rectRounded',
  RectRot = 'rectRot',
  Star = 'star',
  Triangle = 'triangle',
}

export default PointStyleType;
