<template>
  <div class="chart-container" style="position: relative; height:100%; width:100%">
    <canvas id="chart"></canvas>
  </div>
</template>

<script lang="ts">
import Vue from 'vue';
import { Component, Prop, Watch } from 'vue-property-decorator';
import { Chart, ChartData, ChartOptions, ChartType, InteractionItem, Plugin, ScatterDataPoint } from 'chart.js';
import { drag, select } from 'd3';

@Component
export default class ChartJsWrapper extends Vue {
  @Prop({ required: true })
  type!: ChartType;

  @Prop({ required: true })
  data!: ChartData;

  @Prop({ required: true })
  options!: ChartOptions;

  @Prop({ required: true })
  plugins!: Plugin[];

  @Prop({ default: false })
  dragYaxis!: boolean;

  @Prop({ default: 0 }) draggableDataSet!: number;

  mounted() {
    this.resetChart();

    this.$nextTick(() => {
      window.addEventListener('resize', this.windowResized);
    });

    if (this.dragYaxis) {
      const item = select<Element, unknown>(this.chart.canvas);
      item.call(
        drag()
          .container(this.chart.canvas)
          .on('start', (e) => this.getElement(e.sourceEvent, this.chart))
          .on('drag', (e) => this.updateData(e.sourceEvent, this.chart))
          .on('end', (e) => this.dragEndCallback(e.sourceEvent, this.chart)),
      );
    }
  }

  element: InteractionItem | undefined;

  yAxisID: string | undefined;

  getElement(sourceEvent: any, chartInstance: Chart): void {
    if (this.draggableDataSet === -1) {
      // eslint-disable-next-line prefer-destructuring
      this.element = chartInstance.getElementsAtEventForMode(sourceEvent, 'nearest', { intersect: true }, false)[0];
    } else {
      chartInstance.getElementsAtEventForMode(sourceEvent, 'nearest', { intersect: true }, false).forEach((ele) => {
        if (ele.datasetIndex === this.draggableDataSet) {
          this.element = ele;
        }
      });
    }

    if (this.element) {
      const { datasetIndex } = this.element;
      const datasetMeta = chartInstance.getDatasetMeta(datasetIndex);
      this.yAxisID = datasetMeta.yAxisID;
    }
  }

  updateData(sourceEvent: any, chartInstance: Chart): void {
    if (this.element && this.yAxisID) {
      const curDatasetIndex = this.element.datasetIndex;
      const curIndex = this.element.index;

      let y: number | undefined;
      if (sourceEvent.touches) {
        y = this.chart.scales[this.yAxisID].getValueForPixel(
          sourceEvent.touches[0].clientY - chartInstance.canvas.getBoundingClientRect().top,
        );
      } else {
        y = this.chart.scales[this.yAxisID].getValueForPixel(
          sourceEvent.clientY - chartInstance.canvas.getBoundingClientRect().top,
        );
      }
      (this.chart.data.datasets[curDatasetIndex].data[curIndex] as ScatterDataPoint).y = y || 0;
      this.chart.update();
    }
  }

  dragEndCallback(sourceEvent: any, chart: Chart): void {
    if (this.element && this.yAxisID) {
      this.$emit('valueChanged', this.element?.index, this.element?.datasetIndex, chart);
    }
    this.element = undefined;
    this.yAxisID = undefined;
  }

  chart!: Chart;

  resetChart() {
    if (this.chart) {
      this.chart.destroy();
    }
    const point = this.options.elements?.point;
    if (point !== undefined) {
      point.radius = 10;
    }
    this.chart = new Chart(this.$el.firstChild as HTMLCanvasElement, {
      type: this.type,
      data: this.data,
      options: this.options,
      plugins: this.plugins,
    });
  }

  windowResized() {
    this.resetChart();
  }

  beforeDestroy() {
    window.removeEventListener('resize', this.windowResized);
  }

  @Watch('type')
  onTypeChanged() {
    this.$nextTick(() => this.resetChart());
  }

  @Watch('data')
  onDataChanged(newData: ChartData) {
    this.chart.config.data = newData;
    this.chart.update('none');
  }

  @Watch('options')
  onOptionsChanged() {
    this.$nextTick(() => this.resetChart());
  }
}
</script>

<style lang="scss">
canvas.chartjs {
  max-width: 100%;
}
</style>

function drag() { throw new Error('Function not implemented.'); }
