<template>
  <chart-js-wrapper ref="chart" :type="'bar'" :data="chartData" :options="chartOptions" :plugins="[]" />
</template>

<script lang="ts">
/* eslint-disable @typescript-eslint/no-explicit-any */
import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop } from 'vue-property-decorator';
import {
  ChartOptions,
  TooltipModel,
  ChartTypeRegistry,
  TooltipItem,
  ScatterDataPoint,
  BubbleDataPoint,
} from 'chart.js';
import * as Colors from '../assets/colors';
import ChartJsWrapper from './ChartJsWrapper.vue';

@Component({
  components: {
    ChartJsWrapper,
  },
})
export default class BarChart extends Vue {
  @Prop({ required: true, validator: (obj: any) => obj.labels !== undefined && obj.datasets !== undefined })
  dataObject!: any;

  @Prop({ default: 'Probability Density' })
  yAxisLabel!: string;

  @Prop({ default: 'linear' })
  yAxisScale!: string;

  @Prop({ default: '' })
  xAxisLabel!: string;

  @Prop({ default: 'linear' })
  xAxisScale!: string;

  @Prop({ default: 14 })
  chartFontSize!: number;

  @Prop({ default: true })
  displayLegend!: boolean;

  @Prop({ default: false })
  isStacked!: boolean;

  get chartData(): unknown {
    return {
      labels: this.dataObject.labels,
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      datasets: this.dataObject.datasets.map((d: any, i: number) => {
        return {
          label: d.label,
          data: d.data,
          backgroundColor: d.backgroundColor || Colors.COLOR_ARRAY_T[i],
          borderColor: d.borderColor || Colors.COLOR_ARRAY[i],
          borderWidth: 2,
        };
      }),
    };
  }

  get chartOptions(): ChartOptions {
    return {
      responsive: true,
      maintainAspectRatio: false,
      plugins: {
        datalabels: {
          display: false,
        },
        legend: {
          display: this.displayLegend,
          labels: {
            font: {
              size: this.chartFontSize,
            },
            filter(item: any) {
              return item.text !== undefined && item.text.length > 0;
            },
          },
        },
        tooltip: {
          bodyFont: {
            size: this.chartFontSize,
          },
          titleFont: {
            size: 14,
          },
          titleMarginBottom: 10,
          footerMarginTop: 10,
          callbacks: {
            label(tooltipItem: TooltipItem<keyof ChartTypeRegistry>) {
              const totalDataPoints = tooltipItem.dataset.data.reduce(
                (
                  previousValue: number | ScatterDataPoint | BubbleDataPoint | null,
                  currentValue: number | ScatterDataPoint | BubbleDataPoint | null,
                ) => (previousValue as number) + (currentValue as number),
              );
              const dataPointsInBin = Number(Number(tooltipItem.formattedValue).toPrecision(3));
              const percentageInBin = Number(((100 * dataPointsInBin) / (totalDataPoints as number)).toPrecision(3));

              return `${tooltipItem.label || ''} ${dataPointsInBin} (${percentageInBin}%)`;
            },
          },
        },
      },
      scales: {
        x: this.xAxisProperties,
        y: this.yAxisProperties,
      },
      layout: {
        padding: {
          left: 15,
        },
      },
    };
  }

  get xAxisProperties() {
    return {
      scaleLabel: {
        labelString: this.xAxisLabel,
        fontSize: this.chartFontSize + 2,
        fontStyle: 'bold',
        display: this.xAxisLabel.length > 0,
      },
      stacked: this.isStacked,
    };
  }

  get yAxisProperties() {
    return {
      ticks: {
        fontSize: this.chartFontSize,
        suggestedMin: 0,
        callback(value: number | string) {
          if ((value < 0.001 && value > 0) || value >= 1e4) {
            return (value as number).toExponential(1);
          }
          return Number((value as number).toPrecision(3)).toString();
        },
      },
      scaleLabel: {
        labelString: this.yAxisLabel,
        fontSize: this.chartFontSize + 2,
        fontStyle: 'bold',
        display: this.yAxisLabel.length > 0,
      },
      stacked: this.isStacked,
    };
  }
}
</script>
<style scoped></style>
