<template>
  <line-chart
    ref="distChart"
    :chart-series="distributionData"
    :x-axis-label="xAxisLabel"
    :y-axis-label="yAxisLabel"
    :x-axis-scale="xAxisScale"
    :y-axis-scale="yAxisScale"
    :force-x-axis-min-zero="forceXAxisMinZero"
    :data-point-size="showDataPoints ? 0 : 12"
  >
  </line-chart>
</template>

<script lang="ts">
import Vue, { PropType } from 'vue';
import { Component, Prop, Watch } from 'vue-property-decorator';
import Distribution, { DistributionDataGenerator } from 'battelle-common-typescript-statistics';
import { ChartType } from 'chart.js';
import LineChart from './LineChart.vue';
import * as colors from '../assets/colors';
import { IChartSeries } from '../interfaces/data/IChartSeries';

@Component({ components: { LineChart } })
export default class DistributionChart extends Vue {
  @Prop({ required: true }) distributionSeries!: Distribution[];

  @Prop({
    default: () => new DistributionDataGenerator(),
    type: Object as PropType<DistributionDataGenerator>,
  })
  dataGenerator!: DistributionDataGenerator;

  @Prop({ default: 'line' }) requestedChartType!: ChartType;

  @Prop({ default: '' }) xAxisLabel!: string;

  @Prop({ default: '' }) yAxisLabel!: string;

  @Prop({ default: 'linear' }) xAxisScale!: string;

  @Prop({ default: 'linear' }) yAxisScale!: string;

  @Prop({ default: true }) forceXAxisMinZero!: boolean;

  @Prop({ default: false }) showDataPoints!: boolean;

  validation?: any;

  straightLinesDistTypes = [
    'Triangle',
    'Uniform',
    'LogUniform',
    'UniformLimited',
    'PiecewiseUniform',
    'Histogram',
    'TwoUniform',
    'UniformXDependent',
  ];

  steppedLineDistTypes = ['PiecewiseUniform', 'Histogram'];

  // default properties given for a single data set returned from a distribution sample method
  defaultChartProperties(seriesIndex: number, seriesType: ChartType): Partial<IChartSeries> {
    return {
      label: '',
      backgroundColor: colors.COLOR_ARRAY_T[seriesIndex],
      borderColor: 'rgba(255, 255, 255, 0)',
      fill: true,
      useStraightLines: this.straightLinesDistTypes.includes(seriesType),
      stepped: this.steppedLineDistTypes.includes(seriesType),
    };
  }

  /* In the case of a distribution sample method returning multiple data sets (e.g. TwoUniform),
      this method is used so that the series backgrounds match, but the subseries have different line colors */
  multiSeriesDefaultChartProperties(
    seriesIndex: number,
    subSeriesIndex: number,
    seriesType: ChartType,
  ): Partial<IChartSeries> {
    return {
      label: '',
      backgroundColor: colors.COLOR_ARRAY_T[seriesIndex],
      borderColor: colors.PARAM_COLORS[subSeriesIndex],
      fill: true,
      useStraightLines: this.straightLinesDistTypes.includes(seriesType),
      stepped: this.steppedLineDistTypes.includes(seriesType),
    };
  }

  /* For a series object that contains array values for properties, in the case of a sample function
      returning multiple series, this method extracts the property at a given index */
  /* eslint class-methods-use-this: ["error", { "exceptMethods": ["getSingleSeriesProperties"] }] */

  getSingleSeriesProperties(multiSeriesProperties: any, index: number) {
    const series: any = {};
    if (Array.isArray(multiSeriesProperties)) {
      for (let prop = 0; prop < multiSeriesProperties.length; prop += 1) {
        series[prop] = Array.isArray(multiSeriesProperties[prop])
          ? multiSeriesProperties[prop][index]
          : multiSeriesProperties[prop];
      }
    }
    return series;
  }

  get distributionData(): any {
    let distData: any[] = [];

    // Construct distribution data objects with sampled data and default chart properties
    // Overwrite any defaults with any defined properties in the series object
    distData = this.distributionSeries.map((series, index) => {
      // Check if distribution is undefined due to unrecognized type and if all series have a parameters property
      if (!series.IsValid) {
        this.validation = {
          isValid: false,
          message: `Distribution Chart -- The distribution validator and/or sampler are undefined for series ${index}.`,
        };
        return distData;
      }
      this.validation = {
        isValid: true,
      };

      const chartData = this.dataGenerator.PDF(series);

      // If the sample method returns more than one series, then create multiple objects
      if (Array.isArray(chartData[0])) {
        return chartData.map((d, i) => {
          const val = { data: d };
          Object.assign(
            val,
            { pointRadius: 0 },
            this.multiSeriesDefaultChartProperties(index, i, this.requestedChartType),
            this.getSingleSeriesProperties(series, i),
          );
          return val;
        });
      }

      const d = { data: chartData }; // just to get by eslint errors
      return Object.assign(d, { pointRadius: 0 }, this.defaultChartProperties(index, this.requestedChartType), series);
    });

    return this.validation.isValid ? distData : [];
  }

  @Watch('distributionType')
  onDistributionTypeChanged() {
    if (this.$refs.distChart) {
      (this.$refs.distChart as any).resetChart();
    }
  }

  @Watch('validation')
  onValidationChanged(newValid: any) {
    if (!newValid.isValid) {
      this.$emit('onError', newValid);
    }
  }
}
</script>

<style scoped>
#chart {
  flex: 1;
  min-height: 300px;
  max-height: 500px;
}

#error {
  color: red;
  font-size: 1rem;
  font-weight: bold;
  text-align: left;
}
</style>
