import * as colors from '../../assets/colors';
import { IColorProvider } from '../../interfaces/providers/IColorProvider';

export class CycleColorProvider implements IColorProvider {
  constructor(colorChoices?: string[]) {
    this.colorChoices = colorChoices ?? colors.COLOR_ARRAY_T;
    if (this.colorChoices.length <= 0) {
      this.colorChoices.push('rgb(1,1,1)');
    }
  }

  private currentColor = -1;

  private colorChoices: string[];

  public getNextColor(): string {
    this.currentColor += 1;
    if (this.currentColor >= this.colorChoices.length) {
      this.currentColor = 0;
    }
    return this.colorChoices[this.currentColor];
  }
}
