import { ChartType, TooltipOptions } from 'chart.js';
import * as colors from '../../assets/colors';

export const ChartTooltipOptionsDefaultSettings: Partial<TooltipOptions<ChartType>> = {
  enabled: false,
  mode: 'point',
  intersect: false,
  backgroundColor: colors.DARK_GRAY_T,
  padding: 10.0,
  caretSize: 0.0,
  cornerRadius: 10.0,
};

export function CreateDefaultTooltipOptions(): typeof ChartTooltipOptionsDefaultSettings {
  return ChartTooltipOptionsDefaultSettings;
}
